.angle {
    width: 55px;
    height: 35px;
    background: radial-gradient(circle at 0 0, rgba(0,0,0,0) 35px, rgba(255,255,255,0.3) 0);
}

.message {
    max-width: 355px;
    width: 100%;
    height: 95px;
    background-color: rgba(255,255,255,0.3)/*darkslateblue*//*#082103*/;
    border-radius: 10px 10px 10px 0;
    padding: 20px;
    position: relative;
    opacity: 0.8;
}

