.img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
}

.block {
    width: 80px;
    height: 80px;
    margin: 50px 10px 0 10px;
}

.name {
    color: #998FBA;
    font-size: 30px;
    line-height: 15px;
}

.text {
    color: #D8E3D4;
    font-size: 24px;
    line-height: 10px;
}

.time {
    position: absolute;
    font-size: 24px;
    font-weight: bold;
    right: 15px;
    bottom: 10px;
    color: #B5E424/*#274021*/;
}

.status {
    position: absolute;
    font-size: 24px;
    font-weight: bold;
    right: 15px;
    top: -15px;
    color: teal;
}

.hwtitle {
    color: teal;
    margin-left: 30px;
    text-shadow: 4px 4px 10px red, 0 0 2em blue, 0 0 0.5em blue;
}